.container{
    position:absolute;
    width:100%;
    height:100%;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
    transform: translateY(100%);
    transition: transform 0.3s ease-in-out;
}

.container-show{
  transform: translateY(0);
}

.container-about{
  background: rgba(0,0,0,0.8);
}

.container-projects{
  background: #000;
  background: -webkit-linear-gradient(to top, #4b3832, #000);
  background: linear-gradient(to top, #4b3832, #000);
}

.container-contact{
  background: #B2D7B5;
  background: -webkit-linear-gradient(to top, #B2D7B5, #000);
  background: linear-gradient(to top, #B2D7B5, #000);
}

.container-grid{
  width:100%;
  height: 100%;
  box-sizing: border-box;
  overflow-y: scroll;
  padding-top: 100px;
  scroll-behavior: smooth;
}

.container-grid::-webkit-scrollbar {
  width: 1px;     /* Tamaño del scroll en vertical */
  height: 1px;    /* Tamaño del scroll en horizontal */
}

/* Ponemos un color de fondo y redondeamos las esquinas del thumb */
.container-grid::-webkit-scrollbar-thumb {
  background: #fff;
}

/* Cambiamos el fondo y agregamos una sombra cuando esté en hover */
.container-grid::-webkit-scrollbar-thumb:hover {
  background: #fff;
  box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

/* Cambiamos el fondo cuando esté en active */
.container-grid::-webkit-scrollbar-thumb:active {
  background: rgba(0,0,0,0.6);
}

/* Ponemos un color de fondo y redondeamos las esquinas del track */
.container-grid::-webkit-scrollbar-track {
  background: rgba(0,0,0,0.3);
  border-radius: 4px;
}

/* Cambiamos el fondo cuando esté en active o hover */
.container-grid::-webkit-scrollbar-track:hover,
.container-grid::-webkit-scrollbar-track:active{
  background: rgba(0,0,0,0.4);
}

.copyright{
  position:absolute;
  bottom:5px;
  right:10px;

}
.copyright-text{
  cursor:pointer;
  color:#fff;
  font-weight: 200 !important;
}