.about-text-container{
    position:absolute;
    left:12%;
    width:76%;
    height: calc(100vh - 100px);
    color:#fff;
    overflow-y: auto; 
    margin:auto;
}

@media only screen and (max-width: 960px)  and (orientation: portrait){
    .about-text-container {
      width:84%;
      left:5%;
      padding:3%;
      height: calc(100vh - 100px);
      background-color:rgba(0,0,0,0.5);
    }
  }

.about-text{
  font-weight: 200 !important;
  margin-bottom: 20px !important;
}

.slogan-text{
  font-weight: 100 !important;
  margin: 45px !important;
   /* font-family: "Cinzel, serif" !important; */
}

.about-image-container{
  position:absolute;
  top:90px;
  left:0;
  width:500px;
  height: calc(90vh - 100px);
  transform: translateX(-150px);
  box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.7);
  animation: aboutImgStartAnimation 1s ease-in-out;
}

@keyframes aboutImgStartAnimation{
  from{
      opacity:0;
      transform: translateX(-100%);
  }
  to{
      opacity:1;
      transform: translateX(-150px);
  }
}

.about-image-container .about-img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.about-image-overlay-gradient{
  position:absolute;
  bottom:0;
  left:0;
  width:100%;
  height:100%;
  background: #000000;
  background: linear-gradient(to left, rgba(0, 0, 0,0.7), transparent 80%);
}


@media only screen and (max-width: 960px)  and (orientation: portrait){
  .about-image-container {
      top:100px;
      left:0;
      width:100%;
      height:100%;
      transform: translateY(-10px);
  }
  .about-image-container .about-img{
      width: 100%;
      object-fit: cover;
      object-position: center;
  }
}