.menu-container{
    padding-bottom: 40px;
    color:#fff;
    position:relative;
    margin-bottom:40px;
}

.menu-element-nombre{
    height:150px;
    font-family: "Nothing You Could Do" !important;
}

.menu-opcion{
    padding-bottom: 10px;
}

.menu-opcion-nombre{
    font-weight: 200 !important;
    margin-bottom:5px !important;
}

.menu-opcion-descripcion{
    font-family: "Overpass Mono" !important;
}

.menu-element-text-container{
   position:absolute;
   top:0;
   left:5%;
   width:90%;
   height:100%;
   opacity:0;
   margin:auto;
   animation: menuElementTextStartAnimation 1s ease-in-out forwards;
}

@keyframes menuElementTextStartAnimation{
    to{
        opacity:1
    }
}

.menu-element-text-container-hidden{
    top:0;
    left:5%;
    width:90%;
    height:100%;
    opacity:0;
    margin:auto;
}

.menu-element-image-container{
    position:absolute;
    top:60px;
    left:0;
    width:100%;
    height:86%;
    transform: translateX(35%);
    box-shadow: 0px 0px 15px 5px rgba(0,0,0,0.7);
    animation: menuElementImgStartAnimation 1s ease-in-out;
}

@keyframes menuElementImgStartAnimation{
    from{
        opacity:0;
        transform: translateX(100%);
    }
    to{
        opacity:1;
        transform: translateX(35%);
    }
}

.menu-element-image-container .menu-element-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}

.menu-element-image-overlay-gradient{
    position:absolute;
    bottom:0;
    left:0;
    width:100%;
    height:100%;
    background: #000000;
    background: linear-gradient(to right, rgba(0, 0, 0,0.7), transparent 80%);
}


@media only screen and (max-width: 960px)  and (orientation: portrait){
    .menu-element-image-container {
        top:0%;
        left:0;
        width:100%;
        height:150px;
        transform: translateY(-10px);
    }
    .menu-element-image-container .menu-element-img{
        width: 120%;
        object-fit: cover;
        object-position: center;
    }
    .menu-element-image-overlay-gradient{
        background: #000000;
        background: linear-gradient(to bottom, rgba(0, 0, 0,0.8), rgba(0, 0, 0,0.3));
    }
    .menu-element-nombre{
        line-height: 150px !important;
        font-size: 2rem !important;
    }
  }

.menu-grid::-webkit-scrollbar {
    width: 1px;     /* Tamaño del scroll en vertical */
    height: 1px;    /* Tamaño del scroll en horizontal */
}

/* Ponemos un color de fondo y redondeamos las esquinas del thumb */
.menu-grid::-webkit-scrollbar-thumb {
    background: #fff;
}

/* Cambiamos el fondo y agregamos una sombra cuando esté en hover */
.menu-grid::-webkit-scrollbar-thumb:hover {
    background: #fff;
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.2);
}

/* Cambiamos el fondo cuando esté en active */
.menu-grid::-webkit-scrollbar-thumb:active {
    background: rgba(0,0,0,0.6);
}

/* Ponemos un color de fondo y redondeamos las esquinas del track */
.menu-grid::-webkit-scrollbar-track {
    background: rgba(0,0,0,0.3);
    border-radius: 4px;
}

/* Cambiamos el fondo cuando esté en active o hover */
.menu-grid::-webkit-scrollbar-track:hover,
.menu-grid::-webkit-scrollbar-track:active{
    background: rgba(0,0,0,0.4);
}