.location-container{
    height: 360px;
    width:100%;
    position:relative;
    box-shadow: 0px 0px 15px 0px rgba(0,0,0,0.5);
    border: #fff 1px solid;
}

.location-element-image-container{
    width:100%;
    height:100%;
    position:absolute;
    top:0;
    left:-1px;
    overflow:hidden;
    transition: all 0.5s ease-in-out;
}

.location-element-img{
    width: 100%;
    min-height: 100%;
    transform:scale(1.2);
    object-fit: cover;
    object-position: center;
    transition: all 0.5s ease-in-out;
}

.location-element-image-container:hover .location-element-img{
    transform:scale(1);
    transition: all 0.5s ease-in-out;
}

.location-element-actions-container{
    position:absolute;
    bottom:120px;
    height:60px;
    width:100%;
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.button-action:hover{
    box-shadow: 0px 0px 15px 0px rgba(255,255,255,0.5);
    transition: all 0.5s ease-in-out;
}

.button-action{
    cursor:pointer;
    margin: 5px;
    padding:5px;
    border: #fff 1px solid;
    background-color:transparent;
    color:#fff;
    display:flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease-in-out; 
}

.button-action .icon{
    margin-right: 5px;
}

.location-element-image-overlay-gradient{
    position:absolute;
    bottom:120px;
    left:0;
    width:100%;
    height:120px;
    background: rgba(0, 0, 0,0.7);
}

.location-element-map-overlay-gradient{
    position:absolute;
    bottom:0;
    left:0;
    width:100%;
    height:50px;
    background: rgba(0, 0, 0,0.7);
}

.location-element-image-show-map{
    transform: translateX(100%);
    transition: all 0.5s ease-in-out;
}

.location-element-map-container{
    position:relative;
    transform: translateX(-100%);  
    transition: all 0.5s ease-in-out;
}

.location-element-map{
    width: 100%;
    min-height: 100%;
    object-fit: cover;
    object-position: center;
}

.map-marker-outer{
    position:absolute;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top:50%; left:55%;
    width:90px;
    height:90px;
    background-color:rgba(0,0,0,0.3);
    border:#f1f1f1 1px solid;
    border-radius: 50%;
}

.map-marker-middle{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width:45px;
    height:45px;
    background-color:rgba(255,255,255,0.4);
    border:#f1f1f1 1px solid;
    border-radius: 50%;
}

.map-marker{
    width:8px;
    height:8px;
    background-color:#2b2b2b;
    border:#fff 1px solid;
    border-radius:50%;
}

.location-element-map-show-map{
    transform: translateX(0%);
    transition: all 0.5s ease-in-out;
}

.location-element-nombre{
    position:absolute;
    color:#fff;
    width:100%;
    top:120px;
    height:60px;
    line-height:60px !important;
}

.location-element-address{
    position:absolute;
    bottom:0px;
    line-height:50px !important;
    height:50px;
    color:#fff;
    width:100%;   
}

.button-map-volver{
    cursor:pointer;
    position:absolute;
    bottom:0px;
    right:0;
    line-height:50px !important;
    background-color:transparent;
    height:50px !important;
    color:#fff; 
}
