.loader-container{
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    background-color: rgba(255,255,255,0.5);
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.mensaje-container{
    width:80%;
    padding:15px;
    margin:0px auto 15px auto;
    border-radius: 5px;
    box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.5);
}

.mensaje-exito{
    background-color: #4caf50
}

.mensaje-fail{
    background-color: #f44336
}

.mensaje-texto{
    color:#fff;
}