.layout-section {
  width:100vw;
  height:100vh;
  display: flex;
  background-color: #000;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  font-weight: 200;
  -webkit-perspective: 1000;
}

.layout-section .language-selection-div {
  position: absolute;
  top:0;
  left:0;
  height:100%;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

button:focus {outline:0;}

.title-button ,
.layout-section .language-selection-div .button {
  background-color: transparent; /* Green */
  color: #fff;
  height:50px;
  padding: 5px 10px;
  margin: 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border:none;
  opacity:0.5;
  border: 1px solid transparent;
  transition: all 0.5s ease-in-out;
  cursor:pointer;
  position:relative;
}

.title-button{
  opacity:1;
  color: #fff;
  display:flex;
  line-height:50px;
  animation: titleButtonStartAnimation 0.5s ease-in-out;
}

@keyframes titleButtonStartAnimation{
  from{
    opacity:0;
  }
  to{
    opacity:1;
  }
}

.title-button .icon-back {
  color: white;
  align-self: center;
  font-size: 1.4rem;
  margin-right:7px;
  margin-bottom:2px;
}

.layout-section .language-selection-div .button-selected{
  border: 1px solid #940b0b;
  opacity:1;
  cursor:default;
  transform: rotateX(360deg);
  transition:  0s;
}

.layout-section .language-selection-div .moving-underline{
  width:90%;
  left:5%;
  height:1px;
  position:absolute;
  bottom:50%;
  background-color:rgba(255,255,255,0.8);
  transition: all 0.5s ease-in-out;
}

.layout-section .language-selection-div .moving-underline-active{
  background-color:transparent;
  transition: all 0.5s ease-in-out;
}

.layout-section .img-container {
  height:100vh;
  width:500px;
  position: absolute;
  bottom:-60px;
  margin:auto;
  overflow:hidden;
  -webkit-box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.75);
  box-shadow: 0px 0px 25px 0px rgba(0,0,0,0.75);
  animation: imgContainerStartAnimation 1s ease-in-out;
}

.layout-section .img-container-animated{
  animation: imgContainerLoopAnimation 5s ease-in-out infinite;
}

@keyframes imgContainerStartAnimation{
  from{transform: translateY(100%)}
  to{transform: translateY(0)}
}

@keyframes imgContainerLoopAnimation{
  0% {transform: translateY(0)}
  30% {transform: translateY(5%)}
  100% {transform: translateY(0)}
}

@media only screen and (max-width: 960px)  and (orientation: portrait){
  .layout-section .img-container {
    width:100vw;
    height:100vh;
    position: absolute;
    margin:auto;
    bottom:-60px;
  }
}

@media only screen and (max-width: 960px)  and (orientation: landscape){
  .layout-section .img-container {
    width:70vh;
    height:100vh;
  }
} 

.layout-section .img-container .img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.layout-section .img-container .overlay-gradient {
  position:absolute;
  bottom:0;
  left:0;
  width:100%;
  height:100%;
  background: #000000;
  background: linear-gradient(to top, black 5%, transparent 40%, transparent 75%, black 95%)
}

.layout-section .text-container{
  position: absolute;
  top: 15%;
  width:80%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  background-color:rgba(0,0,0,0.7);
}

@media only screen and (max-width: 960px)  and (orientation: portrait){
  .layout-section .text-container {
    bottom: inherit;
    top: 15%;
    background-color:rgba(0,0,0,0.7);
  }
}

.layout-section .title-container{
  color: #fff;
  width:100%;
  margin: 10px 0;
  text-align:center;
}

.layout-section .title-name-container{
  text-align:center;
}

.layout-section .title-container .title-container-separator {
  background-color:#67140b;
  height:2px;
  width:100%;
  margin:10px 0;
  transition: all 1s ease;
  animation: separatorAnimation 1s ease-in-out;
}

@keyframes separatorAnimation {
  0%{
  }
  50% {
    margin-left:50%;width:0%;opacity:0.3;
  }
  100% {
    margin-left:0%;width:100%;opacity:1;
  }
}

.title-container .speciality{
  display:inline-block;
  margin:0 15px;
  font-weight: 200;
}

.layout-section .sections-container{
  position: absolute;
  top: 0;
  width:100%;
  display: flex;
  color: #940b0b; 
  text-align:center;
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  transition: all 0.5s ease-in-out;
}

.sections-buttons-container{
  display:flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  width:100%;
}

.sections-buttons-container-center{
  justify-content: center;
}

@media only screen and (max-width: 960px)  and (orientation: portrait){
  .sections-buttons-container {
    justify-content: center;
  }
}

.layout-section .sections-container-backdrop-active{
  background: #000;
  transition: all 0.5s ease-in-out;
}

.layout-section .sections-container .button {
  background-color: transparent; /* Green */
  color: white;
  padding: 5px 10px;
  margin: 10px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  border: 1px solid rgba(255,255,255,0);
  transition: all 0.5s ease-in-out;
  cursor:pointer;
  animation: buttonStartAnimation 1s ease-in-out;
}

@keyframes buttonStartAnimation{
  from{opacity:0;}
  to{opacity:1}
}

.layout-section .sections-container .button:hover{
  border: 1px solid rgba(255,255,255,0.8);
}

.layout-section .sections-container .button:active{
  transform: rotateX(360deg);
  border: 1px solid rgba(255,255,255,0.8);
  transition:  0s;
}

.layout-section .sections-container .button-selected{
  border: 1px solid rgba(255,255,255,0.8);
}

.layout-section .button-back{
  background-color: transparent; /* Green */
  color: white;
  padding: 5px 10px;
  text-align: center;
  text-decoration: none;
  display: none;
  cursor:pointer;
  border:none;
  transform: translateY(-100%);
  transition: all 1s ease-in-out;
}

.layout-section .button-back-show{
  display: inline-block;
  transform: translateY(0);
}

.layout-section .button-back .icon:hover{
  transform: rotateX(360deg);
  transition: all 0.5s ease-in-out;
}

.layout-section .contact-items-vertical-container{
  position:absolute;
  top:0;
  right:0;
  height:100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  color: #fff;
}

@media only screen and (max-width: 960px)  and (orientation: portrait){
  .layout-section .contact-items-vertical-container{
    top:inherit;
    bottom:35px;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    width:100%;
    height:auto;
  }
}

.layout-section .contact-items-vertical-container .item{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.layout-section .contact-items-vertical-container .item .icon{
  border: 1px solid #fff;
  border-radius:10px;
  background-color: #fff;
  padding:0px;
  margin:2px;
  cursor:pointer;
  transition: all 0.5s ease-in-out;
  widows:50px;
  height:50px;
}

.layout-section .contact-items-vertical-container .item .icon-even{
  animation: iconEvenStartAnimation 1s ease-in-out;
}

.layout-section .contact-items-vertical-container .item .icon-odd{
  animation: iconOddStartAnimation 1s ease-in-out;
}

@keyframes iconEvenStartAnimation {
  from{
    transform: rotateX(0);
    margin:10px;
  }
  to {
    transform: rotateX(360deg);
    margin:2px;
  }
}

@keyframes iconOddStartAnimation {
  from{
    transform: rotateY(0);
    margin:10px;
  }
  to {
    transform: rotateY(360deg);
    margin:2px;
  }
}


.layout-section .contact-items-vertical-container .item .texto{
  opacity:0;
  font-weight: 200;
  margin-right:10px;
  transition: all 0.5s ease-in-out;
}

.layout-section .contact-items-vertical-container .item:hover{
  margin:10px;
  transition: all 0.5s ease-in-out;
}

@media only screen and (max-width: 960px)  and (orientation: portrait){
  .layout-section .contact-items-vertical-container .item:hover{
    margin:0;
    transform: translateY(-10px);
  }
}

.layout-section .contact-items-vertical-container .item:hover .icon-even{
  transform: rotateX(360deg);
}

.layout-section .contact-items-vertical-container .item:hover .icon-odd{
  transform: rotateY(360deg);
}

.layout-section .contact-items-vertical-container .item:hover .texto{
  opacity:1;
}